.wp-block-create-block-podcast {}

.podcast-container {
	display: grid;
	grid-template-columns: 100px 1fr;
	gap: 1rem;

	padding: 2rem;
	box-shadow:  0 0 10px rgba(0, 0, 0, 0.1);
	border-radius: 0.5rem;

	.podcast-cover-img {
		border-radius: 0.5rem;
		overflow: hidden;
		aspect-ratio: 1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.podcast-info {
		display: flex;
		flex-direction: column;
		gap: 0.5rem;
	}

	.podcast-tags {
		display: inline-block;
		font-size: 0.75rem;
		opacity: 0.5;
		margin: 0;
	}

	.podcast-title {
		font-weight: bold;
		font-size: 1.5rem;
		margin: 0;
	}

	.podcast-summary {
		font-size: 0.8rem;
		margin: 0;
		outline-offset: 0px;
	}

	.podcast-player {
		grid-column: 1 / span 2;
		audio{
			width: 100%;
		}
		audio::-webkit-media-controls-panel{
			background-color: white;
		}
	}
}

.loading {
	display: grid;
	grid-template-columns: 100px 1fr;
	gap: 1rem;
	grid-column: span 2;

	.loading-img {
		animation: skeleton-loading 1s linear infinite alternate;
		width: 100px;
		aspect-ratio: 1;
		border-radius: 0.5rem;
	}

		.loading-content {
			display: flex;
			flex-direction: column;
			gap: 1rem;
			width: 100%;

			> div {
				animation: skeleton-loading 1s linear infinite alternate;
				height: 1rem;

				&:nth-child(1) {
					width: 20%;
				}
				&:nth-child(2) {
					height: 1.5rem;
					width: 50%;
				}
			}
		}

}

@keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 80%);
  }
  100% {
    background-color: hsl(200, 20%, 95%);
  }
}